import { useState } from 'react';
import { Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import ContainerUI from '../../../components/ContainerUI/ContainerUI';
import Header from '../../../components/Header/Header';
import InputField from '../../../components/InputField/InputField';
import SnackbarUI from '../../../components/SnackbarUI/SnackbarUI';
import Auth from '../Auth';
import './reset-password.scss';

const ResetPassword = () => {
  const [showMessage, setShowMessage] = useState(false);

  return (
    <Auth className='reset-password-page'>
      <Header authScreen login></Header>
      {showMessage && (
        <SnackbarUI type='success' message='Your password has been reseted!' />
      )}
      <ContainerUI className='my-5 auth-form'>
        <h2 className='mb-4'>Reset your password</h2>
        <Form>
          <InputField
            autoComplete='new-password'
            className='mb-4'
            label='New Password'
            type='password'
            mutedText='Minimum 6 characters'
          />

          <ButtonUI onClick={() => setShowMessage(true)} className='w-100'>
            Reset Password
          </ButtonUI>

          <div className='d-flex d-lg-none align-items-center justify-content-center mt-4'>
            Not a member?&nbsp;
            <Link className='link' href='/register'>
              Sign up now
            </Link>
          </div>
        </Form>
      </ContainerUI>
    </Auth>
  );
};

export default ResetPassword;
