import { QueryClient, QueryClientProvider } from 'react-query';
import './apiAuth';
import Routes from './Routes/Routes';
const queryClient = new QueryClient();
const App = () => {
  return (
    <QueryClientProvider client={queryClient}>
      <div className='App'>
        <Routes></Routes>
      </div>
    </QueryClientProvider>
  );
};

export default App;
