import ContainerUI from '../../../components/ContainerUI/ContainerUI';
import Header from '../../../components/Header/Header';
import GoogleLogin from 'react-google-login';
import InputField from '../../../components/InputField/InputField';
import { Form } from 'react-bootstrap';
import CheckboxUI from '../../../components/CheckboxUI/CheckboxUI';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import IconUI from '../../../components/IconUI/IconUI';
import './login.scss';
import { Link } from 'react-router-dom';
import GoogleLoginButton from '../../../components/ButtonUI/GoogleLoginButton';
import Auth from '../Auth';
import SnackbarUI from '../../../components/SnackbarUI/SnackbarUI';
import { useState } from 'react';
import * as Yup from 'yup';
import { useFormik } from 'formik';
const LoginSchema = Yup.object().shape({
  password: Yup.string().required('Password Required'),
  email: Yup.string().email('Enter valid Email').required('Email Required')
});

const Login = () => {
  const [showVerifyEmail, setShowVerifyEmail] = useState(false);
  const responseGoogle = response => {
    console.log(response);
  };
  const handleSubmit = () => {
    setShowVerifyEmail(true);
    setTimeout(() => {
      setShowVerifyEmail(false);
    }, 10000);
  };
  const formik = useFormik({
    validationSchema: LoginSchema,
    // validateOnBlur: true,
    initialValues: {
      password: '',
      email: ''
    },
    onSubmit: values => {
      handleSubmit();
    }
  });
  return (
    <Auth className='login-page'>
      <Header authScreen login></Header>
      {showVerifyEmail && (
        <SnackbarUI
          type='info'
          message={
            <>
              Verify your email to get the most out of Zignative. Didn't receive
              an email?&nbsp;
              <a className='resend-btn' href='#'>
                Resend confirmation
              </a>
            </>
          }
        />
      )}
      {((formik.touched.password && formik.errors.password) ||
        (formik.touched.email && formik.errors.email)) && (
        <SnackbarUI
          type='error'
          message={
            <>
              {formik.errors.email}, {formik.errors.password}
            </>
          }
        />
      )}
      <ContainerUI className='my-5 auth-form'>
        <h2 className='text-center mb-4'>Sign in to Zignative</h2>
        <Form>
          <GoogleLoginButton
            className='w-100 mb-4'
            buttonText='Sign in with Google'
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
          />
          <div className='hr'>
            <span>Or</span>
          </div>
          <InputField
            autoComplete='new-email'
            className='mb-4'
            label='Username or Email Address'
            name='email'
            value={formik.values.email}
            onChange={formik.handleChange}
            // feedback={formik.touched.email && formik.errors.email}
            // isInvalid={!!formik.errors.email}
          />
          <InputField
            autoComplete='new-password'
            className='mb-4'
            label='Password'
            type='password'
            name='password'
            value={formik.values.password}
            onChange={formik.handleChange}
            // feedback={formik.touched.password && formik.errors.password}
            // isInvalid={!!formik.errors.password}
            rightLabel={
              <Link className='forgot-password' to='/forgot-password'>
                Forgot password?
              </Link>
            }
          />
          <CheckboxUI className='mb-4' label='Remember me' />
          <ButtonUI onClick={formik.handleSubmit} className='w-100'>
            Sign in
          </ButtonUI>
          <div className='d-flex d-lg-none align-items-center justify-content-center mt-4'>
            Not a member?&nbsp;
            <Link className='link' to='/register'>
              Sign up now
            </Link>
          </div>
        </Form>
      </ContainerUI>
    </Auth>
  );
};

export default Login;
